.rules {
  position: relative;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 14.6rem 0 13rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 146px 0 65px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    padding: 5.9rem 0 8rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 7rem 0 8rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 4rem 0 4rem;
  }
}

.rules__title {
  margin: 0 0 5rem;
  text-transform: uppercase;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 1.3;
  letter-spacing: -0.04em;

  .space {
    padding: 0.5rem 0;
    margin: 0 -0.04em;
  }

  .space > .letter {
    transform: translate3d(0, 14rem, 0);
  }

  &.active .space > .letter {
    transform: translate3d(0, 2rem, 0);
  }

  @media (min-width: $stop-scaling) {
    margin-bottom: 50px;
    font-size: 48px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    font-size: 3rem;
    margin-bottom: 2rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
}

.rules__lead {
  margin-left: 5.8rem;
  max-width: 96rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  opacity: 0;
  animation: fadeInUp 1.2s ease forwards 0.25s;

  @media (min-width: $stop-scaling) {
    margin-left: 58px;
    max-width: 960px;
    font-size: 24px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    margin-left: 2.5rem;
    padding-right: 1rem;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    margin-left: 2rem;
    font-size: 2rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    max-width: 100%;
    font-size: 1.4rem;
  }

  p {
    margin: 0;
  }
}

.rules__list {
  @include list-reset;
  counter-reset: li;
  column-count: 2;
  column-gap: 9.6rem;
  margin: auto;
  padding-top: 7rem;
  max-width: 69.6rem;

  @media (min-width: $stop-scaling) {
    column-gap: 96px;
    padding-top: 70px;
    max-width: 696px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    column-count: auto;
    column-gap: 0;
    padding: 0 2.5rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding-top: 0;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    column-gap: 4rem;
  }
}

.rules__item {
  --delay: calc(var(--index) * 0.5s);
  position: relative;
  margin: 0 0 6.2rem;
  padding-top: 0.6rem;
  padding-left: 6.5rem;
  break-inside: avoid;
  will-change: transform; // fix columns overflow bug

  @media (min-width: $stop-scaling) {
    margin: 0 0 62px;
    padding-top: 6px;
    padding-left: 65px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    margin-bottom: 1rem;
    padding-left: 5rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    margin-bottom: 2rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding-left: 5rem;
    font-size: 1.2rem;
  }

  &:nth-child(1) {
    &::before {
      animation-delay: 0.7s;
    }
  }

  &:nth-child(2) {
    &::before {
      animation-delay: 0.9s;
    }
  }

  &:nth-child(3) {
    &::before {
      animation-delay: 1.1s;
    }
  }

  &:nth-child(4) {
    &::before {
      animation-delay: 1.3s;
    }
  }

  &:nth-child(1) p {
    animation-delay: 0.85s;
  }

  &:nth-child(2) p {
    animation-delay: 1.15s;
  }

  &:nth-child(3) p {
    animation-delay: 1.25s;
  }

  &:nth-child(4) p {
    animation-delay: 1.45s;
  }

  &::before {
    content: counter(li);
    counter-increment: li;
    position: absolute;
    top: 0;
    left: 0;
    width: 4.5rem;
    height: 4.5rem;
    font-family: $font-alt;
    font-weight: 400;
    font-size: 2.4rem;
    background-color: $c-purple;
    border-radius: 50%;
    line-height: 5rem;
    text-align: center;
    color: $c-light;
    transform: scale(0);
    will-change: transform;
    animation: scaleRuleItem 0.65s cubic-bezier(0.35, 1.55, 0.65, 1) forwards;

    @media (max-width: $tablet) and (orientation: portrait) {
      top: 1rem;
      width: 2.5rem;
      height: 2.5rem;
      line-height: 3rem;
      font-size: 1.4rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      top: 0.6rem;
      width: 2.5rem;
      height: 2.5rem;
      line-height: 3rem;
      font-size: 1.4rem;
    }
  }

  p {
    margin: 0;
    opacity: 0;
    transform: translateX(2rem);
    will-change: transform;
    animation: visualItem 0.75s cubic-bezier(0.35, 1, 0.65, 1)  forwards;

    @media (max-width: $tablet) and (orientation: portrait) {
      min-height: calc(1.4rem * 2.2);
    }
  }
}

.rules__link {
  position: absolute;
  bottom: 6rem;
  right: 5.6rem;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
    animation: rulesLink 0.45s cubic-bezier(0.25, 1, 0.9, 1) forwards;
  }


  &::before {
    display: none;
  }

  span {
    opacity: 0;
  }

  &.animated {
    &::before {
      display: block;
      animation: expand 0.7s ease;
    }

    span {
      animation: fade-in 1s ease 0.7s forwards;
    }
  }

  @media (min-width: $stop-scaling) {
    bottom: 60px;
    right: 56px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    left: 0;
    right: 0;
    bottom: 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    bottom: 5rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    bottom: 2rem;
    right: 1.5rem;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes slide-up {
  0% {
    transform: translateY(30%);
  }

  100% {
    transform: none;
  }
}

@keyframes slide-left-short {
  0% {
    transform: translateX(30%);
  }

  100% {
    transform: translateX(0);
  }
}


@keyframes grow {
  0% {
    height: 0;
  }

  100% {
    height: 100%;
  }
}

@keyframes bounce-rules {
  0% {
    transform: scale(0);
  }

  80% {
    transform: scale(1.125);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes expand {
  0% {
    width: 40%;
  }

  100% {
    width: 100%;
  }
}
