@keyframes slide-in-blurred-right {
  0% {
    transform: translateX(1000rem) scaleX(2.5) scaleY(0.2);
    transform-origin: 0 50%;
    opacity: 0;
  }
  100% {
    transform: translateX(0) scaleY(1) scaleX(1);
    transform-origin: 50% 50%;
    opacity: 1;
  }
}

@keyframes increase-width-from-right {
  from {
    width: 0%;
  }

  to {
    width: calc(100% - 21rem);
  }
}

@keyframes increase-width-from-right-delay {
  0% {
    width: 0%;
  }

  30% {
    width: 0%;
  }

  100% {
    width: calc(100% - 21rem);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(2.5rem);
  }

  30% {
    opacity: 0;
    transform: translateY(2.5rem);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes translateScreen {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes text-focus-in {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes scaleRuleItem {
  0% {
    transform: scale(0);
    color: transparent;
  }
  50% {
    color: $c-light;
  }
  100% {
    transform: scale(1);
  }
}

@keyframes rulesLink {
  0% {
    width: 0;
    color: transparent;
  }

  95% {
    color: $c-dark;
  }

  100% {
    width: 18rem;
  }
}

@keyframes visualItem {
  from {
    opacity: 0;
    transform: translateX(2rem);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes opacity {
  from {
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes suitcase {
  0% {
    width: 300px;
    transform: translateY(500px);
  }

  90% {
    width: 550px;
    transform: translateY(-20px);
  }

  100% {
    width: 600px;
    transform: translateY(0);
  }
}

@keyframes flamingo {
  0% {
    transform: translate3d(5px, 500px, 0);
  }

  20% {
    transform: translate3d(5px, -10px, 0);
  }

  50% {
    transform: translate3d(-5px, -5px, 0) rotateZ(3deg);
  }

  80% {
    transform: translate3d(0, 0, 0) rotateZ(0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes leftleaf {
  0% {
    opacity: 0;
    transform: rotateZ(15deg) translate3d(0, -30px, 0);
  }

  10% {
    opacity: 0;
    transform: rotateZ(15deg) translate3d(0, -30px, 0);
  }

  30% {
    opacity: 1;
    transform: rotateZ(-5deg) translate3d(0, 0, 0);
  }

  50% {
    transform: rotateZ(5deg);
  }

  70% {
    transform: rotateZ(-3deg);
  }

  90% {
    transform: rotateZ(3deg);
  }

  100% {
    transform: rotateZ(0);
  }
}

@keyframes rightleaf {
  0% {
    opacity: 0;
    transform: rotateZ(15deg) translate3d(0, -30px, 0);
  }

  10% {
    opacity: 0;
    transform: rotateZ(15deg) translate3d(0, -30px, 0);
  }

  30% {
    opacity: 1;
    transform: rotateZ(-5deg) translate3d(0, 0, 0);
  }

  40% {
    transform: rotateZ(5deg);
  }

  60% {
    transform: rotateZ(-3deg);
  }

  80% {
    transform: rotateZ(3deg);
  }

  100% {
    transform: rotateZ(0);
  }
}
